.modal {
    --transition-time: 0.3s;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 1000;
    //min-width: 290px;
    background-color: #133C50F2;
    cursor: pointer;
    overflow-y: auto;
    overflow-x: hidden;
    text-align: center;
    opacity: 0;
    visibility: hidden;
    transition: opacity var(--transition-time) linear, visibility var(--transition-time) linear;

    &--basket {
        display: flex;
        justify-content: flex-end;
        opacity: 1;
        visibility: visible;
        overflow-y: hidden;
    }

    &__container {
        position: relative;
        z-index: 10000;
        margin: 50px auto;
        padding: 56px 30px 40px 30px;
        display: none;
        vertical-align: middle;
        border-radius: 24px;
        background-color: #fff;
        cursor: default;
        text-align: center;

        @include mobile {
            padding: 46px 15px 40px 15px;
        }

        &.modal-open {
            display: inline-block;
        }
    }

    &::before {
        content: '';
        display: inline-block;
        vertical-align: middle;
        height: 100%;
    }
    
    &.is-open {
        opacity: 1;
        visibility: visible;
        transition: opacity var(--transition-time) linear, visibility var(--transition-time) linear;
    }

    &__close {
        position: absolute;
        right: 30px;
        top: 30px;
        width: 16px;
        height: 16px;
        cursor: pointer;

        @include mobile {
            right: 20px;
            top: 20px;
        }
    }
}

.disable-scroll {
	overflow: hidden;
	height: 100vh;
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
}

.fade {
    opacity: 0;
    transition: opacity var(--transition-time) linear;
}

.fade.animate-open {
    opacity: 1;
    transition: opacity var(--transition-time) linear;
}

.fadeInUp {
    opacity: 0;
    transform: translateY(-100px);
    transition: opacity var(--transition-time), transform var(--transition-time);
}

.fadeInUp.animate-open {
	opacity: 1;
	transform: translateY(0);
	transition: opacity var(--transition-time), transform var(--transition-time);
}

.modal-basket.fadeInUp {
    opacity: 1;
    transform: translateY(0);
    transform: translateX(100%);
}

.modal-basket.fadeInUp.animate-open {
    transform: translateX(0);
}