.reviews {
    padding: 130px 0;

    @include minitablet {
        padding: 70px 0;
    }

    &__container {
        max-width: 1660px;

        @include mobile {
            padding: 0;
            margin: 0;
            width: 100%;
        }
    }
    
    &__article {
        padding: 100px 81px 146px 81px;
        border-radius: 24px;
        background-color: #EFEFEF;

        @include bigdesktop {
            padding: 80px 40px 100px 40px;
        }

        @include desktop {
            padding: 60px 40px;
        }

        @include tablet {
            padding: 40px 30px;
        }

        @include mobile {
            padding: 30px 15px;
        }
    }

    &__swiper-container {
        position: relative;
        min-width: 0;

        @include tablet {
            margin-bottom: 30px;
        }
    }

    &__swiper {
        height: 100%;
    }

    &__slide {
        height: calc((100% - 18px) / 2) !important;

        @include tablet {
            height: 100%;
        }

        @include minitablet {
            width: 240px;
        }
    }

    .swiper-pagination {
        bottom: -52px;

        @include tablet {
            display: none;
        }
    }

    .slider-nav {
        @include minitablet {
            display: none;
        }
    }

    .slider-nav__prev {
        left: -12px;
    }

    .slider-nav__next {
        right: -6px;
    }

    &__content {
        padding: 30px;
        border: 1px solid #D4D4D4;
        border-radius: 24px;
        background-color: #fff;

        @include tablet {
            padding: 20px 10px;
        }

        @include mobile {
            padding: 30px 10px;
        }
    }

    &__descr {
        margin: 0;
        margin-bottom: 58px;
        font-size: 16px;
        font-weight: 400;
        line-height: 24px;
        color: #272B2C;

        @include tablet {
            margin-bottom: 30px;
        }

        @include mobile {
            font-size: 14px;
            line-height: 21px;
        }
    }

    &__block {
        display: flex;
        align-items: center;
        gap: 10px;
    }

    &__image {
        @include mobile {
            width: 46px;
            height: 46px;
        }
    }

    &__author {
        margin-bottom: 5px;
        font-size: 18px;
        font-weight: 700;
        line-height: 18px;
        color: #272B2C;

        @include mobile {
            font-size: 16px;
            line-height: 16px;
        }
    }

    &__time {
        font-size: 16px;
        font-weight: 400;
        line-height: 16px;
        color: #BBBBBB;

        @include mobile {
            font-size: 14px;
            line-height: 14px;
        }
    }
}

.reviews-pagination {
    z-index: 1;

    .swiper-pagination-bullet {
        width: 168.25px !important;
        height: auto !important;
        border-radius: none;
        background-color: transparent;
        opacity: 1;
    }

    &__svg {
        position: relative;
        width: 100%;
        height: 20px;

        &::before {
            content: '';
            position: absolute;
            left: 0;
            top: -5px;
            z-index: 1;
            width: 100%;
            height: 20px;
            background-color: #DD3824;
            transform-origin: left;
            transform: scaleX(0);
            transition: transform 0.3s ease-in-out;
        }

        svg {
            width: 100%;
            height: 20px;
            fill: none;
            stroke: #0C5E84;
            stroke-width: 3px;
            overflow: hidden;
        }
    }

    .swiper-pagination-bullet-active {
        .reviews-pagination__svg {
            svg {
                stroke: #DD3824; 
            }
        }
    }
}

