.articles {
    &__block {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 18px;
        align-items: stretch;

        @include desktop {
            width: auto;
            overflow-x: scroll;
        }

        @include tablet {
            margin-bottom: 30px;
        }

        @include mobile {
            gap: 10px;
        }
    }
}

.articles-item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;

    @include desktop {
        width: 320px;
    }

    @include tablet {
        width: 280px;
    }

    @include mobile {
        width: 240px;
    }

    &:nth-child(2) {
        .articles-item__img {
            &::before {
                background-image: url('../img/date-2.png'); 
            }
        }
    }

    &:nth-child(3) {
        .articles-item__img {
            &::before {
                background-image: url('../img/date-3.png'); 
            }
        }
    }

    &:nth-child(4) {
        .articles-item__img {
            &::before {
                background-image: url('../img/date-4.png'); 
            }
        }
    }

    &__img {
        position: relative;
        margin-bottom: 20px;
        margin-top: auto;

        &::before {
            content: '';
            position: absolute;
            left: 1px;
            bottom: 4px;
            width: 12.5%;
            height: 17.5%;
            background-image: url('../img/date-1.png');
            background-size: cover;
            background-position: left bottom;
            background-repeat: no-repeat;
        }
    }

    &__subtitle {
        margin-bottom: 20px;
        font-size: 20px;
        font-weight: 700;
        line-height: 22px;
        text-transform: uppercase;
        color: #272B2C;
        transition: color 0.3s ease-in-out;

        &:hover {
            color: #0C5E84;
        }

        @include tablet {
            font-size: 18px;
            line-height: 19px;
        }

        @include mobile {
            font-size: 16px;
            line-height: 17.6px;
        }
    }

    &__descr {
        margin: 0;
        font-size: 16px;
        font-weight: 400;
        line-height: 20.8px;
        color: #272B2C;

        @include mobile {
            font-size: 14px;
            line-height: 18.2px;
        }
    }
}