.rating {
    position: relative;
    display: flex;
    align-items: flex-end;
    gap: 6px;
    margin-bottom: 10px;
    font-size: 32px;
    line-height: 0.7;

    @include mobile {
        flex-direction: column;
        align-items: flex-start;
    }

    &__content {
        display: flex;
        align-items: center;
        gap: 6px;
    }

    @include mobile {
        margin-bottom: 15px;
        font-size: 26px;
        line-height: 1;
    }

    &__body {
        position: relative;

        &::before {
            content: '★★★★★';
            display: block;
            color: #EFEFEF;
        }
    }

    &__items {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        display: flex;
    }

    &__item {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 10;
        flex: 0 0 20%;
        height: 100%;
        opacity: 0;
    }

    &__active {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        overflow: hidden;

        &::before {
            content: '★★★★★';
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            color: #FFCC00;
        }
    }

    &__value {
        font-size: 13px;
        font-weight: 400;
        line-height: 13px;
        color: #272B2C;
    }

    &__descr {
        font-size: 13px;
        font-weight: 400;
        line-height: 13px;
        color: #272B2C;
    }
}
