[class*="ground-pane"] {
    -webkit-filter: grayscale(1);
    filter: grayscale(1);
    border-radius: 24px;
}

[class*="copyrights-pane"] {
    display: none !important;
}

.stores {
    &__content {
        position: relative;
        border-radius: 24px;
    }

    &__container {
        @include mobile {
            padding: 0;
            margin: 0;
            width: 100%;
        }
    }

    &__info {
        position: absolute;
        top: 40px;
        left: 40px;
        z-index: 10;
        padding: 40px 30px;
        border-radius: 24px;
        width: 414px;
        background-color: #0C5E84;

        @include tablet {
            bottom: 0;
            left: 15px;
            top: auto;
        }

        @include mobile {
            left: 15px;
            right: 15px;
            padding: 30px 20px;
            width: calc(100% - 30px);
        }
    }

    &__map {
        border-radius: 24px;
        height: 650px;
        width: auto;

        @include tablet {
            height: 881px;
        }
    }

    &__title {
        margin-bottom: 30px;
        font-size: 48px;
        line-height: 100%;

        @include tablet {
            font-size: 42px;
        }
    
        @include minitablet {
            margin-bottom: 20px;
            font-size: 36px;
        }

        @include mobile {
            font-size: 26px;
        }
    }

    &__item {
        &:not(:last-child) {
            margin-bottom: 20px;
        }
    }

    &__descr {
        margin-bottom: 10px;
        font-size: 18px;
        font-weight: 700;
        line-height: 100%;
        color: #fff;
        opacity: 0.5;

        @include tablet {
            font-size: 16px;
        }

        @include mobile {
            margin-bottom: 5px;
            font-size: 14px;
        }
    }

    &__item-content {
        font-size: 16px;
        font-weight: 400;
        line-height: 100%;
        color: #FFFFFF;

        @include tablet {
            font-size: 14px;
        }

        @include mobile {
            font-size: 12px;
        }
    }
}

#map > ymaps > ymaps > ymaps > ymaps.ymaps-2-1-79-places-pane > ymaps:nth-child(1) > ymaps > ymaps {
    position: relative;

    &::before {
        content: 'улица Кирилла Россинск...';
        position: absolute;
        left: 110%;
        top: 50%;
        padding: 6px;
        font-size: 14px;
        font-weight: 600;
        line-height: 100%;
        color: #272B2C;
        background-color: #fff;
        width: 240px;
        height: auto;
        transform: translateY(-50%);
    }
}