.trust {
    padding-bottom: 130px;

    @include minitablet {
        padding-bottom: 70px;
    }

    &__container {
        @include mobile {
            padding: 0;
            margin: 0;
            width: 100%;
        }
    }

    &__content {
        position: relative;
        padding: 40px;
        border-radius: 24px;
        background-color: #F6F6F6;

        @include desktop {
            padding: 30px 20px;
        }

        @include mobile {
            padding: 30px 15px;
        }
    }

    &__waves {
        position: absolute;
        top: 78px;
        right: 40px;
        width: 276px;
        height: 38px;

        @include tablet {
            display: none;
        }
    }

    &__title {
        color: #272B2C;
        max-width: 424px;
    }

    &__block {
        display: flex;
        gap: 18px;
        align-items: stretch;

        @include tablet {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
        }

        @include minitablet {
            grid-template-columns: 1fr;
        }
    }

    &__item {
        &:not(:last-child) {
            padding: 30px 20px;
            border-radius: 24px;
            background-color: #fff;
            max-width: 336px;

            @include tablet {
                max-width: none;
            }
        }

        &:last-child {
            @include tablet {
                grid-column: 1 / 3;
            }

            @include minitablet {
                grid-column: 1 / 2;
            }
        }

        @include tablet {
            width: 100%;
        }
    }

    &__svg {
        margin-bottom: 30px;
        width: 60px;
        height: 60px;
    }

    &__subtitle {
        margin-bottom: 10px;
        font-size: 20px;
        font-weight: 700;
        line-height: 22px;
        text-transform: uppercase;
        color: #272B2C;

        @include desktop {
            font-size: 18px;
            line-height: 19.8px;
        }
    }

    &__descr {
        margin: 0;
        font-size: 16px;
        font-weight: 400;
        line-height: 20.8px;
        color: #272B2C;

        @include desktop {
            font-size: 14px;
            line-height: 18.2px;
        }
    }
}

.trust-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 40px 28px;
    align-self: flex-start;

    @include mobile {
        grid-template-columns: 1fr;
    }

    &__caption {
        margin-bottom: 10px;
        font-size: 56px;
        font-weight: 600;
        line-height: 100%;
        text-transform: uppercase;
        color: #0C5E84;

        @include desktop {
            font-size: 46px;
        }
    }

    &__descr {
        margin: 0;
        font-size: 16px;
        font-weight: 400;
        line-height: 20.8px;
        color: #272B2C;

        @include desktop {
            font-size: 14px;
            line-height: 18.2px;
        }
    }
}

.caption-age {
    max-width: 150px;

    @include desktop {
        max-width: none;
    }
}