.modal-basket {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1002;
    width: 100%;
    height: 100vh;
    background: transparent;
    z-index: 1000;
    transform: translateX(100%);
    margin: 0;
    overflow-y: scroll;
    transition: transform 0.4s ease-in-out, background 0.4s ease-in-out;

    &--open {
        transform: translateX(0);
    }

    .modal__close {
        z-index: 100;
    }

    &__content {
        position: absolute;
        top: 0;
        right: 0;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-between;
        padding: 56px 30px 50px 30px;
        border-radius: 24px 0 0 24px;
        width: 715px;
        height: auto;
        min-height: 100%;
        background-color: #FFF;

        @include tablet {
            width: 80%;
        }

        @include mobile {
            padding: 46px 15px 40px 15px;
            width: 90%;
        }
    }

    &__title {
        margin-bottom: 30px;
        font-size: 36px;
        font-weight: 600;
        line-height: 100%;
        color: #272B2C;
        text-align: left;
    }

    &__blocks {
        width: 100%;
    }

    &__block {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        margin-bottom: 120px;
        padding: 15px 50px 15px 15px;
        border: 1px solid #D4D4D4;
        border-radius: 24px;
        width: 100%;

        @include tablet {
            margin-bottom: 60px;
        }

        @include minitablet {
            margin-bottom: 40px;
            flex-direction: column;
            align-items: flex-start;
        }

        @include mobile {
            justify-content: flex-start;
            margin-bottom: 20px;
            padding: 10px 10px 15px 10px;
            height: 280px;
        }

        &:not(:last-child) {
            margin-bottom: 10px;
        }
    }

    &__center {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }

    &__img {
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid #EFEFEF;
        border-radius: 24px;
        width: 140px;
        height: 140px;
        min-width: 140px;

        @include mobile {
            width: 120px;
            height: 120px;
            min-width: 120px; 
        }
    }

    &__link {
        display: inline-block;
        margin-bottom: 20px;
        font-size: 18px;
        font-weight: 700;
        line-height: 19.8px;
        color: #272B2C;
        text-align: left;
        max-width: 400px;
        transition: color 0.3s ease-in-out;

        @include minitablet {
            display: none;
        }

        &:hover {
            color: #0C5E84;
        }
    }

    &__hidden-link {
        display: none;
        margin-bottom: 0;
        font-size: 18px;
        font-weight: 700;
        line-height: 19.8px;
        color: #272B2C;
        text-align: left;
        transition: color 0.3s ease-in-out;

        @include mobile {
            font-size: 15px;
            line-height: 16.5px;
        }

        &:hover {
            color: #0C5E84;
        }

        @include minitablet {
            display: block;
        }
    }

    &__wrap {
        display: flex;
        align-items: center;
        gap: 20px;

        @include mobile {
            gap: 10px;
        }
    }

    &__counter {
        display: flex;
        align-items: center;
        gap: 20px;

        @include minitablet {
            flex-direction: column;
            align-items: flex-start;
            gap: 10px;
        }
    }

    &__price {
        font-size: 18px;
        font-weight: 700;
        line-height: 100%;
        color: #272B2C;
    }

    &__basket {
        margin-bottom: 0;
        margin-left: 32px;

        @include bigdesktop {
            flex-direction: row;
            align-items: center;
            gap: 15px;
        }

        @include tablet {
            margin-left: 0;
        }
    }

    &__icons {
        position: absolute;
        right: 25px;
        top: 37px;
        bottom: 37px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        gap: 54px;
        height: auto;

        @include mobile {
            left: 10px;
            right: 10px;
            top: auto;
            bottom: 15px;
            flex-direction: row;
            height: 22px;
            width: calc(100% - 20px);
        }
    }

    &__icon {
        svg {
            width: 22px;
            height: 20px;
        }
    }

    &__bottom {
        margin-top: auto;
    }
}

.basket-bottom {
    &__descr {
        display: flex;
        align-items: center;
        gap: 15px;
        margin-bottom: 30px;
        font-size: 28px;
        font-weight: 600;
        line-height: 100%;
        color: #272B2C;

        @include minitablet {
            margin-bottom: 20px;
            flex-wrap: wrap;
            font-size: 26px;
        }

        div > span {
            color: #0C5E84; 
        }

        span {
            text-transform: uppercase;
        }
    }

    &__btns {
        display: flex;
        gap: 18px;

        @include minitablet {
            flex-direction: column;
        }
    }

    &__btn {
        padding: 18px 36px;
        border-radius: 16px;
        border: 2px solid #DD3824;
        font-size: 16px;
        font-weight: 600;
        line-height: 100%;
        color: #DD3824;

        &:last-child {
            color: #fff;
            background-color: #DD3824;
        }
    }
}