.vodotok {
    &__container {
        @include mobile {
            padding: 0;
            margin: 0;
            width: 100%;
        }
    }

    &__content {
        position: relative;
        padding: 40px;
        border-radius: 24px;
        background-color: #0C5E84;

        @include minitablet {
            padding: 30px;
        }

        @include mobile {
            padding: 30px 15px;
        }

        &::before {
            content: '';
            position: absolute;
            bottom: 0;
            right: 30px;
            height: 74%;
            width: 40%;
            background-image: url('../img/vodotok-bg.png');
            background-size: contain;
            background-position: right bottom;
            background-repeat: no-repeat;

            @include bigdesktop {
                height: 60.8%;
                width: 32%;
            }

            @include desktop {
                width: 50%;
            }

            @include mobile {
                right: 0;
                width: 304px;
                height: 350px;
                background-image: url('../img/vodotok-bg-sm.png');
            }
        }

        &::after {
            content: '';
            position: absolute;
            left: 30%;
            top: -20%;
            z-index: 1;
            width: 100%;
            //height: 1398px;
            height: 130%;
            opacity: 0.5;
            mix-blend-mode: soft-light;
            background: radial-gradient(50% 50% at 50% 50%, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%);

            @include tablet {
                top: -10%;
            }
        }
    }

    &__waves {
        position: absolute;
        top: 78px;
        right: -15px;
        width: 286px;
        height: 40px;

        @include tablet {
            display: none;
        }
    }

    &__waves-mobile {
        display: none;
        position: absolute;
        top: 46px;
        right: 0;
        width: 72.45px;
        height: 19.35px;

        @include tablet {
            display: block;
        }
    }

    &__title {
        max-width: 590px;

        @include tablet {
            max-width: 90%;
        }

        @include mobile {
            margin-bottom: 30px;
        }
    }

    &__block {
        display: flex;
        align-items: center;
        gap: 30px;

        @include bigdesktop {
            align-items: flex-start;
        }

        @include desktop {
            flex-direction: column;
        }

        @include tablet {
            gap: 60px;
        }
    }

    &__left {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 18px;

        @include mobile {
            grid-template-columns: 1fr;
        }
    }

    &__item {
        padding: 20px;
        border-radius: 24px;
        width: 336px;
        background-color: #FFFFFF;

        @include desktop {
            width: auto;
        }
    }

    &__svg {
        margin-bottom: 58px;
        width: 66px;
        height: 66px;

        @include minitablet {
            margin-bottom: 50px;
        }
    }

    &__descr {
        margin: 0;
        font-size: 20px;
        font-weight: 700;
        line-height: 22px;
        color: #272B2C;

        @include minitablet {
            font-size: 18px;
            line-height: 19.8px;
        }
    }

    &__right {
        position: relative;
        z-index: 5;
        width: 100%;
    }

    &__text {
        padding-bottom: 140px;
        font-size: 32px;
        font-weight: 700;
        line-height: 35.2px;
        color: #FFF;
        max-width: 310px;
        text-transform: uppercase;

        @include desktop {
            padding-bottom: 360px;
            font-size: 30px;
            line-height: 33px;
        }

        @include tablet {
            padding-bottom: 280px;
            font-size: 26px;
            line-height: 28px;
        }

        @include minitablet {
            padding-bottom: 200px;
            font-size: 24px;
            line-height: 26px;
        }

        @include mobile {
            font-size: 20px;
            line-height: 22px;
            max-width: 191px;
        }
    }
}