.brands {
    position: relative;

    &__container {
        max-width: 1660px;

        @include mobile {
            padding: 0;
            margin: 0;
            width: 100%;
        }
    }

    &__content {
        position: relative;
        padding: 70px 81px 100px 81px;
        border-radius: 24px;
        background-color: #0C5E84;

        @include bigdesktop {
            padding: 60px 60px 92px 60px;
        }

        @include tablet {
            padding: 60px 40px 92px 40px;
        }

        @include mobile {
            padding: 60px 15px 92px 15px;
        }

        &::before {
            content: '';
            position: absolute;
            left: 80px;
            top: -50%;
            bottom: -50%;
            right: 80px;
            z-index: 1;
            width: calc(100% - 160px);
            height: 200%;
            mix-blend-mode: soft-light;
            background: radial-gradient(50% 50% at 50% 50%, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%);
        }
    }

    &__title {
        margin-bottom: 50px;

        @include minitablet {
            margin-bottom: 30px;
        }
    }

    &__swiper-content {
        position: relative;
        min-width: 0;
    }

    &__slider-nav {
        @include minitablet {
            display: none;
        }
    }

    &__slide {
        border-radius: 24px;
        background-color: #FFFFFF;

        @include mobile {
            display: flex;
            align-items: center;
            justify-content: center;
            height: 103px;
        }
    }

    &__img {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 160px;

        @include mobile {
            padding: 15px;
        }

        img {
            object-fit: cover;

            @include mobile {
                height: 42px;
                object-fit: contain;
            }
        }
    }

    .slider-nav__btn {
        &::before {
            top: -16px;
            left: -16px;
            width: 50px;
            height: 50px;
        }
    }

    .slider-nav__prev {
        left: -12px;
    }

    .slider-nav__next {
        right: -6px;
    }
}