.news {
    padding: 130px 0;

    @include minitablet {
        padding: 70px 0;
    }

    &__container {
        margin-right: 0;
        padding-right: 25px;
        max-width: 1710px;
    }

    &__swiper-container {
        position: relative;
        min-width: 0;

        @include tablet {
            margin-bottom: 30px;
        }
    }

    &__swiper {
        width: 100%;
    }

    .swiper-wrapper {
        display: flex;
        align-items: stretch;

        @include desktop {
            gap: 18px;
            width: 100%;
            overflow-x: scroll;
        }

        @include mobile {
            gap: 10px;
        }
    }

    &__slide {
        flex: 1 0 auto;
        height: auto;
        width: calc((100% - 35% - 54px) / 3) !important;
        max-height: 387px;
        transition: width 0.5s ease-in-out, height 0.5s ease-in-out, opacity 0.5s ease-in-out;

        &:nth-child(2n) {
            .news__img {
                &::before {
                    background-image: url('../img/date-6.png');
                }
            }
        }

        &:nth-child(3n) {
            .news__img {
                &::before {
                    background-image: url('../img/date-7.png');
                }
            }
        }

        &:nth-child(4n) {
            .news__img {
                &::before {
                    background-image: url('../img/date-8.png') !important;
                }
            }
        }

        &--last {
            opacity: 0.3;

            .news__subtitle {
                cursor: auto;

                &:hover {
                    color: #272B2C;
                }
            }

            @include desktop {
                opacity: 1;
            }
        }

        @include desktop {
            width: 320px !important;
            max-height: none;
        }

        @include tablet {
            width: 280px !important;
        }
    
        @include mobile {
            width: 240px !important;
        }
    }

    .swiper-slide-active {
        width: 35% !important;
        height: 540px;
        max-height: none;

        @include bigdesktop {
            height: 470px;
        }
    }

    .slider-nav {
        position: absolute;
        display: flex;
        gap: 58px;
        z-index: 15;
        top: auto !important;
        bottom: 60px !important;
        left: calc(35% + 39px);
        transform: translateY(-100%);

        @include bigdesktop {
            bottom: 0 !important;
        }

        @include desktop {
            display: none;
        }
    }

    .slider-nav__prev {
        position: static;
    }

    .swiper-button-disabled {
        svg {
            fill: #DD3824;
        }

        &::before {
            border: 2px solid #DD3824;
        }
    }

    .slider-nav__next {
        position: static;
    }

    &__img {
        position: relative;
        margin-bottom: 20px;
        width: 100%;
        height: 100%;

        &::before {
            content: '';
            position: absolute;
            left: 1px;
            bottom: 4px;
            width: 12.5%;
            height: 17.5%;
            background-image: url('../img/date-5.png');
            background-size: cover;
            background-position: left bottom;
            background-repeat: no-repeat;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__subtitle {
        margin-bottom: 20px;
        font-size: 20px;
        font-weight: 700;
        line-height: 22px;
        color: #272B2C;
        transition: color 0.3s ease-in-out;

        &:hover {
            color: #0C5E84;
        }

        @include desktop {
            font-size: 18px;
            line-height: 20px;
        }

        @include mobile {
            font-size: 16px;
            line-height: 17.6px;
        }
    }

    &__descr {
        font-size: 16px;
        font-weight: 400;
        line-height: 20.8px;
        color: #272B2C;

        @include mobile {
            font-size: 14px;
            line-height: 18.2px;
        }
    }
}

.news-pagination {
    z-index: 10;
    bottom: 80px !important;
    left: auto !important;
    right: 15% !important;
    text-align: right !important;

    @include bigdesktop {
        right: 0 !important;
        bottom: 20px !important;
    }

    @include desktop {
        display: none;
    }

    .swiper-pagination-bullet {
        width: 168.25px !important;
        height: auto !important;
        border-radius: none;
        background-color: transparent;
        opacity: 1;
    }

    &__svg {
        position: relative;
        width: 100%;
        height: 20px;

        svg {
            width: 100%;
            height: 20px;
            fill: none;
            stroke: #0C5E84;
            stroke-width: 3px;
            overflow: hidden;
        }
    }

    .swiper-pagination-bullet-active {
        .news-pagination__svg {
            svg {
                stroke: #DD3824; 
            }
        }
    }
}