.header-mobile {
    position: fixed;
	z-index: 100;
	top: 70px;
	padding-top: 20px;
	padding-bottom: 100px;
	width: 100%;
	height: 100%;
    z-index: 150;
	overflow-y: scroll;
	background-color: #fff;
	transform: translateY(-150%);
	transition: all 0.4s ease-in;

    &--active {
        transform: translateY(0);
    }

	&__container {
		max-width: 600px;
	}

	&__content {
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	.catalog {
		display: flex;
		justify-content: center;
		width: 100%;
	}

	.tel {
		display: flex;
		margin-bottom: 30px;

		&__arrow {
			display: none;
		}
	}

	.header-form {
		display: flex;
		margin-bottom: 30px;
		padding: 13.5px 16px;
		width: 100%;

		&__input {
			&::placeholder {
				color: #272B2C;
			}
		}
	}

	&__top {
		margin-bottom: 30px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100%;
	}

	&__menu {
		display: none !important;
	}

	.menu--visible {
		display: grid !important;
		margin-bottom: 0;
	}

	.address {
		&__text {
			color: #272B2C;
		}

		&__svg {
			fill: #272B2C;
		}
	}

	.header-links {
		margin-right: 0;
	}

	.nav {
		margin-top: 30px;
		align-self: start;

		&__list {
			flex-direction: column;
			align-items: start;
			gap: 20px;
		}

		&__link {
			color: #272B2C;
		}
	}
}