/* stylelint-disable */

@mixin bigdesktop {
    @media (max-width: (1440px)) {
        @content;
    }
}

@mixin desktop {
    @media (max-width: (1200px)) {
        @content;
    }
}

@mixin tablet {
    @media (max-width: (1024px)) {
        @content;
    }
}

@mixin minitablet {
    @media (max-width: (768px)) {
        @content;
    }
}

@mixin mobile {
    @media (max-width: (576px)) {
        @content;
    }
}