.header-bottom {
    position: relative;
    z-index: 400;
    background-color: #fff;
    
    &__content {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 10px;
        padding: 21px 0;
    }

    &__logo {
        align-self: flex-start;
        width: 211.21px;
        height: 58px;

        @include bigdesktop {
            width: 190px;
            height: 46.4px;
        }

        @include desktop {
            width: 126.6px;
            height: 34.8px;
        }

        @include mobile {
            width: 91.04px;
            height: 25px;
        }
    }

    &__tel {
        display: flex;
        align-items: center;
        gap: 10px; 

        @include desktop {
            flex-direction: column;
        }

        @include tablet {
            display: none;
        }
    }

    &__tel-link {
        display: flex;
        align-items: center;
        gap: 10px;
        font-size: 22px;
        font-weight: 600;
        line-height: 100%;
        color: #272B2C;

        @include bigdesktop {
            font-size: 18px;
        }
    }

    &__btn {
        font-size: 12px;
        font-weight: 700;
        line-height: 12px;
        color: #DD3824;
    }

    &__arrow {
        svg {
            width: 6px;
            height: 6px;
        }
    }
}

.catalog {
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 12px 24px;
    border-radius: 16px;
    background-color: #DD3824;
    cursor: pointer;

    @include bigdesktop {
        padding: 12px 16px;
    }

    @include tablet {
        display: none;
    }

    span {
        font-size: 16px;
        font-weight: 600;
        line-height: 19.5px;
        color: #fff;
        text-transform: uppercase;
    }
}

.tel {
    display: flex;
    align-items: center;
    gap: 10px; 

    @include desktop {
        flex-direction: column;
    }

    @include tablet {
        display: none;
    }

    &__link {
        display: flex;
        align-items: center;
        gap: 10px;
        font-size: 22px;
        font-weight: 600;
        line-height: 100%;
        color: #272B2C;
    
        @include bigdesktop {
            font-size: 18px;
        }
    }

    &__link-wrap {
        position: relative;

        &:hover {
            .tel__popup {
                opacity: 1;
                visibility: visible;
            }
        }
    }
    
    &__btn {
        font-size: 12px;
        font-weight: 700;
        line-height: 12px;
        color: #DD3824;
    }
    
    &__arrow {
        svg {
            width: 6px;
            height: 6px;
        }
    }
}

.tel-popup {
    position: absolute;
    top: -30px;
    left: -12%;
    display: flex;
    flex-direction: column;
    padding: 16px;
    border-radius: 8px;
    width: 239px;
    background-color: #fff;
    box-shadow: 4px 4px 14px 0px #0000001F;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

    &__link {
        font-size: 22px;
        font-weight: 600;
        line-height: 100%;
        color: #272B2C;

        &:not(:last-child) {
            margin-bottom: 20px;
        }
    }
}

.header-links {
    display: flex;
    align-items: center;
    gap: 20px;

    @include tablet {
        gap: 12px;
        margin-left: auto;
        margin-right: 12px;
    }

    &__link {
        position: relative;

        .tel__popup {
            z-index: 1000;
            left: 50%;
            transform: translateX(-50%);
        }

        &:hover,
        &:active {
            .tel__popup {
                opacity: 1;
                visibility: visible;
            }
        }
        
        &:nth-child(1) {
            display: none;
    
            @include tablet {
                display: block;
            }
        }
    
        &:nth-child(3), &:nth-child(5) {
            @include tablet {
                display: none;
            }
        }
    }
    
    &__counter {
        position: absolute;
        top: -2px;
        right: -4px;
        z-index: 10;
        align-items: center;
        justify-content: center;
        padding: 1px 2px;
        border-radius: 12px;
        min-width: 12px;
        height: 12px;
        font-size: 9px;
        font-weight: 400;
        line-height: 9px;
        text-align: center;
        color: #fff;
        background-color: #DD3824;
        display: none;
    
        &:not(:empty) {
            display: flex;
        }
    }
    
    &__svg {
        svg {
            width: 28px;
            height: 28px;
        }
    }
}

.header-form {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 13.5px 13.5px 13.5px 16px;
	border-bottom: 1px solid #272B2C;
    width: 27.2%;

    @include bigdesktop {
        padding: 10px 10px 13px 10px;
        width: 22%;
    }

    @include desktop {
        width: 27%;
    }

    @include tablet {
        display: none;
    }

    &__label {
        width: 100%;
    }

	&__input {
        border: none;
        width: calc(100% - 20px);
        color: #2A2F3C;

        &:focus {
            outline: none;
        }

        &::placeholder {
            color: #9097A7;
        }
	}

    &__btn {
        width: 23px;
        height: 21px;
        background-image: url('../img/search.png');
		background-size: cover;
		background-position: right center;
		background-repeat: no-repeat;
    }
}

.burger-wrapper {
    border: 2px solid #0C5E84;
    border-radius: 100%;
    width: 30px;
    height: 30px;
    display: none;
    transition: color 0.3s ease-in-out;

    &--active {
        border-color: #DD3824;
    }

    @include tablet {
        display: flex;
        align-items: center;
        justify-content: center;
    }
}

.burger {
    @include burger;

    &--mobile {
        position: relative;
        --burger-width: 16px !important;
        --burger-height: 12px !important;
        color: #DD3824 !important;

        &::before,
        &::after {
            width: 16px;
        }
    }
}