.links {
    position: fixed;
    right: 50px;
    //right: calc((100% - 1398px) / 2);
    bottom: 50px;
    z-index: 100;
    display: none;

    @include bigdesktop {
        right: 0;
    }

    &--active {
        display: block;

        @include tablet {
            display: none;
        }
    }

    &__link {
        display: block;
        cursor: pointer;

        svg {
            width: 60px;
            height: 60px;
        }

        &:not(:last-child) {
            margin-bottom: 20px;
        }
    }
}