.header-catalog {
    padding: 0;
    width: 100%;
    height: 0;
    background-color: #fff;
    transform: translateY(-100%);
    transition: transform 0.4s ease-in-out, height 0.4s ease-in-out;

    @include tablet {
        display: none;
    }

    &--visible {
        padding: 20px 0 40px 0;
        transform: translateY(0);
        height: 100%;
        transition: transform 0.4s ease-in-out, height 0.4 ease-in-out;
    }
}

.menu {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 0 18px;
    background-color: #fff;

    @include tablet {
        grid-template-columns: repeat(2, 1fr);
    }

    @include mobile {
        grid-template-columns: 1fr;
    }

    &__link {
        display: flex;
        align-items: center;
        gap: 10px;
        padding: 10px;
        border-radius: 8px;
        background-color: #FFFFFF;
        transition: background 0.3s ease-in-out;

        &:hover {
            background-color: #F6F6F6;

            .menu__descr {
                color: #0C5E84;
            }
        }
    }

    &__img {
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 8px;
        border: 1px solid #EFEFEF;
        width: 60px;
        min-width: 60px;
        height: 60px;
        background-color: #FFFFFF;
    }

    &__descr {
        font-size: 18px;
        font-weight: 600;
        line-height: 100%;
        color: #272B2C;
        transition: color 0.3s ease-in-out;
    }
}