.footer {
    &__container {
        max-width: 1660px;

        @include mobile {
            padding: 0;
            margin: 0;
            width: 100%;
        }
    }

    &__content {
        padding: 70px 81px 30px 81px;
        border-radius: 24px 24px 0px 0px;
        background-color: #133C50;

        @include bigdesktop {
            padding: 60px 40px 30px 40px;
        }

        @include mobile {
            padding: 60px 15px;
        }
    }

    &__grid {
        margin-bottom: 30px;
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        gap: 20px;

        @include desktop {
            flex-wrap: wrap;
            gap: 30px;
        }

        @include tablet {
            margin-bottom: 0;
        }

        @include mobile {
            display: grid;
            grid-template-columns: 1fr;
            align-items: center;
        }
    }

    &__block {
        width: auto;

        &:not(:first-child) {
            padding-top: 40px;

            @include desktop {
                padding-top: 0;
            }
        }

        &:first-child {
            @include desktop {
                width: 100%;
                flex: 1 0 auto;
                max-width: 100%;
            }
        }
    }

    &__subtitle {
        position: relative;
        display: inline-block;
        margin-bottom: 30px;
        font-size: 22px;
        font-weight: 700;
        line-height: 22px;
        text-transform: uppercase;
        color: #FFFFFF;
        text-align: left;

        @include mobile {
            margin-bottom: 0;
        }

        &::before {
            content: '';
            position: absolute;
            right: -12px;
            top: 6px;
            z-index: 1;
            width: 5.45px;
            height: 10px;
            background-image: url('../img/arrow-white.png');
            background-size: cover;
            background-position: right center;
            background-repeat: no-repeat;
            display: none;

            @include mobile {
                display: block;
            }
        }

        &--open {
            margin-bottom: 30px;
            
            &::before {
                transform: rotate(180deg);
            }
        }
    }

    &__item {
        &:not(:last-child) {
            margin-bottom: 20px;
        }
    }

    &__link {
        font-size: 15px;
        font-weight: 600;
        line-height: 15px;
        color: #FFFFFF80;
    }

    &__company {
        font-size: 15px;
        font-weight: 600;
        line-height: 15px;
        text-align: center;
        color: #FFFFFF80;

        @include tablet {
            display: none;
        }
    }

    &__menu {
        @include mobile {
            display: none;
        }

        &--open {
            @include mobile {
                display: block;
            }
        }
    }
}

.footer-left {
    max-width: 433px;

    @include mobile {
        display: grid;
        grid-template-columns: 1fr;
    }

    &__hidden {
        display: none;

        @include tablet {
            display: block;
        }
    }

    &__logo {
        width: 211.21px;
        height: 58px;
        min-width: 211px;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__icon {
        width: 46px;
        height: 46px;
        opacity: 0.5;
        transition: opacity 0.3s ease-in-out;

        &:hover {
            opacity: 1;
        }

        @include mobile {
            width: 40px;
            height: 40px;         
        }
    }

    &__logo-block {
        margin-bottom: 30px;
        display: flex;
        align-items: center;
        gap: 15px;

        @include desktop {
            max-width: 420px;
        }

        @include mobile {
            flex-direction: column;
            align-self: center;
        }
    }

    &__logo-line {
        flex: 1 0 auto;
        align-self: flex-end;
        height: 52px;
        width: 1px;
        background-color: #fff;

        @include mobile {
            align-self: center;
            width: 100%;
            height: 1px;
            max-width: 212px;
            text-align: center;
        }
    }

    &__logo-descr {
        font-size: 13px;
        font-weight: 400;
        line-height: 16.9px;
        color: #FFFFFF80;
        text-transform: uppercase;

        @include mobile {
            text-align: center;
        }
    }

    &__icons {
        margin-bottom: 30px;
        display: flex;
        gap: 15px;

        @include mobile {
            gap: 10px;
        }
    }

    &__descr {
        margin-bottom: 15px;
        font-size: 16px;
        font-weight: 400;
        line-height: 16px;
        color: #FFFFFF;

        &:last-child {
            margin-bottom: 30px;
        }
    }

    &__link {
        display: inline-block;
        font-size: 16px;
        font-weight: 400;
        line-height: 16px;
        color: #FFFFFF;

        &:first-child {
            text-decoration: underline;
        }

        &:not(:last-child) {
            margin-bottom: 15px;
        }
    }

    &__links {
        margin-bottom: 30px;
    }
}

.footer-cards {
    display: flex;
    gap: 20px;
    justify-content: space-between;
    margin-bottom: 30px;

    @include desktop {
        max-width: 413px;
    }

    @include mobile {
        flex-direction: column;
        align-items: center;
    }

    &__left {
        display: flex;
        gap: 15px;
        align-items: center;
    }

    &__btn {
        padding: 12px 20px;
        border-radius: 8px;
        font-size: 16px;
        font-weight: 600;
        line-height: 16px;
        color: #FFFFFF;
        background-color: #DD3824;
        transition: background 0.3s ease-in-out;

        &:focus {
            background-color: #C92B17;
        }

        &:hover {
            background-color: #C92B17;
        }
    }
}

.footer-address {
    @include tablet {
        grid-row: 3 / 4;
        margin-bottom: 30px;
    }

    &__descr {
        margin-bottom: 5px;
        font-size: 16px;
        font-weight: 400;
        line-height: 16px;
        color: #FFFFFF80;
    }
}

.address {
    display: flex;
    align-items: center;
    gap: 5px;

    &__svg {
        width: 18.33px;
        height: 22px;
    }

    &__descr {
        font-size: 14px;
        font-weight: 600;
        line-height: 17.07px;
        color: #FFFFFF;
        font-style: normal;
    }
}

.footer-bottom {
    margin-bottom: 30px;
    display: flex;
    align-items: center;
    gap: 4px;
    flex-wrap: wrap;
    padding: 16px 0 18px 0;
    border-top: 1px solid #FFFFFF80;
    border-bottom: 1px solid #FFFFFF80;

    @include tablet {
        display: none;
    }

    &__link, &__address, &__table {
        font-size: 16px;
        font-weight: 600;
        line-height: 16px;
        color: #fff;
    }

    &__line {
        height: 14px;
        width: 1px;
        background-color: #FFFFFF80;
    }
}

.footer-hidden {
    display: none;

    .footer-bottom__line {
        display: none;
    }

    @include tablet {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 15px;
        padding: 16px 0;
        border-top: 1px solid #FFFFFF80;
        border-bottom: 1px solid #FFFFFF80;
    }
}