.search {
    padding: 20px 16px 20px 20px;
    width: 555px;

    .modal__close {
        top: 36px;
        right: 14px;
    }

    &__scroll {
        padding-right: 17px;
        height: 826px;
    }

    &__form {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 20px;
        padding: 13.5px 16px;
        border: 1px solid #D4D4D4;
        width: 490px;
    }

    &__label {
        width: 100%;
    }

    &__input {
        border: none;
        width: 100%;
        font-size: 16px;
        font-weight: 600;
        line-height: 100%;
        color: #272B2C;

        &:focus {
            outline: none;
        }

        &::placeholder {
            font-size: 16px;
            font-weight: 600;
            line-height: 100%;
            color: #D4D4D4;
        }
    }

    &__btn {
        width: 22px;
        height: 21px;
        background-image: url('../img/search.png');
        background-size: cover;
        background-position: right center;
        background-repeat: no-repeat;
    }

    &__title-block {
        margin-bottom: 20.5px;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__subtitle {
        margin-bottom: 0;
        font-size: 22px;
        font-weight: 600;
        line-height: 100%;
        color: #272B2C;
    }

    &__clean {
        font-size: 16px;
        font-weight: 600;
        line-height: 100%;
        color: #0C5E84;
    }

    &__title {
        margin-bottom: 20px;
        font-size: 22px;
        font-weight: 600;
        line-height: 100%;
        text-align: left;
        color: #272B2C;
    }

    &__cards {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 10px;
    }

    .goods-slide {
        width: 240px;

        &__content {
            padding: 10px;
            box-shadow: none;
        }

        &__top {
            margin-bottom: 20px;
        }

        &__img {
            padding: 10px;
            height: 220px;
        }

        &__img-mobile {
            display: block;
        }

        &__subtitle {
            font-size: 14px;
            line-height: 15.4px;
            text-align: left;
        }

        &__bottom-content {
            margin-top: 10px;
        }

        &__bottom {
            min-height: 254px;
        }

        .rating {
            gap: 3px;
            font-size: 28px;

            &__content {
                gap: 3px;
            }
        }

        .price-block {
            &__price {
                text-align: left;
            }
        }

        .basket {
            gap: 12px; 

            @include bigdesktop {
                flex-direction: row;
                align-items: center;
            }

            &__link {
                font-size: 12px;
                line-height: 100%;
            }
        }
    }
}

.search-history {
    &__item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 20.5px;
        padding: 12px;
        border-radius: 8px;
        background-color: #F6F6F6;

        &:not(:last-child) {
            margin-bottom: 2px;
        }
    }

    &__descr {
        padding-left: 40px;
        padding-top: 5px;
        font-size: 15px;
        font-weight: 600;
        line-height: 100%;
        text-align: left;
        color: #0C5E84;
        height: 24px;
        background-image: url('../img/clock.png');
        background-size: 24px 24px;
        background-position: center left;
        background-repeat: no-repeat;
        text-transform: uppercase;
    }
}