.title-block {
    margin-bottom: 36px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    &:hover .title-block__svg-wrap {
        margin-left: -8px;
        padding-left: 8px;
    }

    &:hover {
        .title-block__link-descr {
            color: #0C5E84;
        }
    }

    @include minitablet {
        margin-bottom: 0;
    }

    &__title {
        margin-bottom: 0;
        color: #272B2C;

        @include minitablet {
            margin-bottom: 30px;
        }
    }
    
    &__icon {
        display: flex;
        align-items: center;
        justify-content: center;
        padding-left: 6px;
        border: 2px solid #DD3824;
        border-radius: 50px;
        width: 60px;
        height: 60px;
    }
    
    &__svg {
        height: 8.73px;
        width: 24.3px;
        fill: #DD3824;
    }

    &__link {
        display: flex;
        align-items: center;
        gap: 15px;

        @include tablet {
            display: none;
        }
    }

    &__svg-wrap {
        display: inline-block;
        overflow: hidden;
        vertical-align: middle;
        transition: 0.3s margin-left linear, 0.3s padding-left linear;  

        svg {
            margin-right: 4px;
            margin-left: -8px;
        }
    }
    
    &__link-descr {
        font-size: 18px;
        font-weight: 700;
        line-height: 18px;
        color: #272B2C;
        text-transform: uppercase;
        transition: color 0.3s ease-in-out;
    }

    &__hidden {
        display: none;

        @include tablet {
            display: flex;
            gap: 15px;
            justify-content: flex-start;
        }

        .title-block__link-descr {
            flex: 0 1 auto;
            font-size: 16px;
            line-height: 16px;

            @media (max-width: 420px) {
                max-width: 190px;
            }
        }

        .title-block__icon {
            padding-left: 0;
            width: 36px;
            height: 36px;
            min-width: 36px;
            border-radius: 100%;
        }

        .title-block__svg {
            margin-right: 0;
            margin-left: 0;
            width: 16px;
        }
    }
}


