.footer-catalog {
    width: 1398px;

    @include bigdesktop {
        width: 1000px;
    }

    @include tablet {
        width: 90%;
    }

    @include mobile {
        padding: 46px 0 40px 0;
    }

    &__title {
        color: #272B2C;
        text-align: left;

        @include mobile {
            padding-left: 15px;
        }
    }

    &__block {
        display: flex;
        align-items: flex-start;
        gap: 46px;
        width: 100%;

        @include desktop {
            gap: 20px;
        }
    }

    &__left {
        @include minitablet {
            display: none;
        }
    }

    &__right-wrap {
        width: 100%;
    }

    &__link {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 12px 16px;
        border-radius: 8px;
        width: 324px;
        background-color: #fff;
        transform: rotate(0);
        transition: background 0.3s ease-in-out;

        &--active {
            background-color: #F6F6F6;

            .footer-catalog__img {
                visibility: visible;
                opacity: 1;

                @include minitablet {
                    transform: rotate(180deg);
                }
            }
        }

        @include bigdesktop {
            width: 200px;
        }

        @include minitablet {
            width: 100%;
            background-color: #F6F6F6;

            .footer-catalog__img {
                visibility: visible;
                opacity: 1;
            }
        }
    }

    &__img {
        visibility: hidden;
        opacity: 0;
        transition: visibility 0.3s ease-in-out, opacity 0.3s ease-in-out
    }

    &__link-descr {
        font-size: 20px;
        font-weight: 700;
        line-height: 22px;
        color: #272B2C;
        text-align: left;
    }

    .menu {
        padding-right: 28px;
        width: 100%;
        opacity: 0;
        visibility: hidden;
        height: 0;
        overflow: hidden;
        transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

        @include minitablet {
            padding-right: 0;            
            opacity: 1;
            visibility: visible;
            height: auto;
            overflow: auto;
        }

        @include minitablet {
            grid-template-columns: 1fr;
        }

        &--active {
            opacity: 1;
            visibility: visible;
            height: 654px;
            overflow: auto;
            transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

            @include minitablet {
                height: auto;
            }
        }

        .footer-catalog__link {
            display: none;

            @include minitablet {
                display: flex;
            }
        }

        &__wrap {
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            gap: 18px;
            align-items: stretch;

            @include bigdesktop {
                grid-template-columns: repeat(3, 1fr);
            }

            @include tablet {
                grid-template-columns: repeat(2, 1fr);
            }

            @include minitablet {
                display: none;
                grid-template-columns: 1fr; 
                gap: 0;
            }

            &--active {
                @include minitablet {
                    display: grid;
                }
            }
        }

        &__descr {
            font-size: 15px;
            line-height: 100%;

            @include minitablet {
                text-align: left;
            }
        }

        &__img {
            @include mobile {
                width: 40px;
                height: 40px;
                min-width: 40px;
            }

            img {
                @include mobile {
                    width: 29.33px;
                    height: 28.67px;
                }
            }
        }

        &__link {
            display: flex;
            flex-direction: column;
            align-items: center;
            padding: 10px 16px;
            height: 100%;
            height: 150px;
            border-radius: 8px;
            border: 1px solid #EFEFEF;
            transition: border 0.3s ease-in-out;

            &:hover {
                border-color: #0C5E84;
                background-color: #fff;
            }

            @include minitablet {
                flex-direction: row;
                gap: 10px;
                padding: 10px;
                border: none;
                height: 60px;
                background-color: #FFFFFF;
                transition: background 0.3s ease-in-out;

                &:hover {
                    .menu__descr {
                        color: #0C5E84;
                    }
                }
            }
        }
    }
}