.seo {
    padding-bottom: 130px;

    @include minitablet {
        padding-bottom: 70px;
    }

    &__content {
        display: flex;
        gap: 24px;
        align-items: stretch;

        @include minitablet {
            flex-direction: column-reverse;
            gap: 30px;
        }
    }

    &__left {
        padding-right: 100px;
        max-width: 926px;
        height: 490px;
        width: calc(100% - 100px);

        @include desktop {
            padding-right: 80px;
        }

        @include tablet {
            padding-right: 50px;
        }

        @include minitablet {
            padding-right: 0;
            width: 100%;
            height: auto;
        }
    }

    &__title {
        margin-bottom: 30px;
        color: #272B2C;
    }

    &__descr {
        margin: 0;
        margin-bottom: 30px;
        font-size: 18px;
        font-weight: 400;
        line-height: 27px;
        color: #272B2C;

        &--1 {
            margin-bottom: 10px;
        }

        @include tablet {
            font-size: 16px;
            line-height: 24px;
        }

        @include mobile {
            font-size: 14px;
            line-height: 21px;
        }
    }

    &__subtitle {
        margin-bottom: 20px;
        font-size: 32px;
        font-weight: 600;
        line-height: 32px;
        color: #272B2C;
        text-transform: uppercase;
    }

    &__hidden {
        @include minitablet {
            display: none;
        }

        &--open {
            display: block;
        }
    }

    &__open {
        display: none;

        @include minitablet {
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 10px;
        }
    }

    &__open-text {
        font-size: 18px;
        font-weight: 700;
        line-height: 19.8px;
        color: #272B2C;
        text-align: center;
    }
}

.seo-info {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 60px 40px;
    border-radius: 24px;
    width: 454px;
    background-color: #0C5E84;

    @include minitablet {
        width: 100%;
    }

    @include mobile {
        padding: 60px 20px;
    }

    &::before {
        content: '';
        position: absolute;
        left: 0;
        bottom: -24px;
        width: 984.97px;
        height: 984.97px;
        mix-blend-mode: soft-light;
        background: radial-gradient(50% 50% at 50% 50%, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%);
    }

    &__title {
        margin-bottom: 10px;
        font-size: 48px;
        font-weight: 600;
        line-height: 52.8px;
        text-transform: uppercase;
        color: #FFFFFF;
        text-align: center;

        @include tablet {
            font-size: 40px;
            line-height: 44px;
        }

        @include minitablet {
            font-size: 36px;
            line-height: 40px;
        }

        @include mobile {
            font-size: 32px;
            line-height: 35.2px;
        }
    }

    &__descr {
        margin: 0;
        margin-bottom: 20px;
        font-size: 20px;
        font-weight: 400;
        line-height: 22px;
        color: #FFFFFF;
        text-align: center;

        @include mobile {
            font-size: 18px;
            line-height: 19.8px;
        }
    }

    &__link {
        display: inline-block;
        margin-bottom: 20px;
        font-size: 24px;
        font-weight: 600;
        line-height: 100%;
        color: #FFFFFF;

        &:last-child {
            margin-bottom: 26px;

            @include mobile {
                margin-bottom: 30px;
            }
        }
    }

    &__btn {
        padding: 18px 80px;
        border-radius: 16px;
        font-size: 16px;
        font-weight: 600;
        line-height: 100%;
        text-transform: uppercase;
        color: #FFFFFF;
        background-color: #DD3824;
        transition: background 0.3s ease-in-out;

        @include mobile {
            padding: 18px 76.5px;
        }

        &:focus {
            background-color: #C92B17;
        }

        &:hover {
            background-color: #C92B17;
        }
    }
}