.header-top {
    position: relative;
    z-index: 500;
    
    @include tablet {
        display: none;
    }

    &__content {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 20px 26px;
        border-radius: 0px 0px 20px 20px;
        background-color: #133C50;
    }

    &__left {
        position: absolute;
        top: 0;
        left: -25px;
        width: 25px;
        height: 25px;
        background-color: #133C50;

        &::before {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 25px;
            height: 25px;
            border-top-right-radius: 500px;
            background-color: white;
        }
    }

    &__right {
        position: absolute;
        top: 0;
        right: -25px;
        width: 25px;
        height: 25px;
        background-color: #133C50;

        &::before {
            content: '';
            position: absolute;
            bottom: 0;
            right: 0;
            width: 25px;
            height: 25px;
            border-top-left-radius: 500px;
            background-color: white;
        }
    }
}

.address {
    display: flex;
    align-items: center;
    gap: 5px;

    &__svg {
        width: 18.33px;
        height: 22px;
        fill: #fff;
    }
    
    &__text {
        font-size: 14px;
        font-weight: 600;
        line-height: 17.07px;
        color: #fff;
    }
}

.address-wrap {
    position: relative;
    z-index: 1000000;

    /*&:hover {
        .address-popup {
            visibility: visible;
            opacity: 1;
        }
    }*/
}

.address-popup {
    position: absolute;
    z-index: 10000000;
    top: 30px;
    left: 10px;
    padding: 32px 16px 20px 16px;
    border-radius: 8px;
    background-color: #fff;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0.3s ease-in-out, opacity 0.3s ease-in-out;

    &--active {
        visibility: visible;
        opacity: 1; 
    }

    &::before {
        content: '';
        position: absolute;
        top: -16px;
        left: 12px;
        width: 32px;
        height: 30px;
        background-image: url('../img/triangle.png');
        background-position: left top;
        background-size: cover;
        background-repeat: no-repeat;
    }

    &__close {
        position: absolute;
        top: 16px;
        right: 16px;
    }

    &__subtitle {
        margin-bottom: 10px;
        font-size: 20px;
        font-weight: 700;
        line-height: 100%;
        text-align: left;
        color: #272B2C;
        width: 322px;
        text-transform: none;
    }

    &__btns {
        display: flex;
        gap: 16px;
    }

    &__btn {
        padding: 12px 20px;
        border-radius: 8px;
        font-size: 16px;
        font-weight: 600;
        line-height: 100%;
        color: #fff;
        background-color: #DD3824;

        &:last-child {
            border: 2px solid #DD3824;
            color: #DD3824;
            background-color: #fff;
        }
    }
}