.goods {
    &--offer {
        padding: 110px 0;

        @include minitablet {
            padding: 70px 0;
        }

        .goods-slide__bottom {
            @include minitablet {
                min-height: 310px;
            }
        }

        .goods__tab--checked {
            background-color: #0081CC;
        }

        .goods__list {
            @include mobile {
                flex-direction: row;
                gap: 10px;
            }
        }

        .goods__tab {
            @include mobile {
                width: auto;
            }
        }

        .goods-slide__img {
            img {
                @include mobile {
                    max-height: 113.37px;
                }
            }
        }
    }

    &__title {
        color: #272B2C;
        max-width: 445px;

        @include mobile {
            margin-bottom: 30px;
        }
    }

    &__list {
        margin-bottom: 0;
        display: flex;
        flex-wrap: wrap;
        gap: 10px;

        @include minitablet {
            margin-bottom: 20px;
        }

        @include mobile {
            flex-direction: column;
            align-items: stretch;
            gap: 5px;
        }
    }

    &__tab {
        padding: 15px;
        border-radius: 8px;
        font-size: 15px;
        font-weight: 700;
        line-height: 100%;
        color: #272B2C;
        background-color: #F6F6F6;
        text-transform: uppercase;

        @include mobile {
            padding: 10px;
            font-size: 12px;
            width: 100%;
            text-align: left;
        }

        &--checked {
            color: #FFFFFF;
            background-color: #0C5E84;
        }
    }

    &__swiper-content {
        position: relative;
        min-width: 0;
    }

    &__swiper {
        padding: 20px 0;

        @include minitablet {
            padding: 0;
        }
    }

    .offers-swiper {
        padding: 20px 0;

        @include minitablet {
            padding: 0;
        }
    }

    .swiper-wrapper {
        align-items: stretch;

        @include minitablet {
            flex-wrap: wrap;
            gap: 20px 10px;
            box-sizing: border-box;
        }
    }

    .slider-nav__btn {
        top: 40%;

        @include minitablet {
            display: none;
        }
    }

    .slider-nav__prev {
        left: -12px;
    }

    .slider-nav__next {
        right: -6px;
    }

    &__open {
        position: relative;
        z-index: 100;
        display: none;
        margin-top: 20px;
        width: 100%;

        @include minitablet {
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 10px;
        }
    }

    &__open-text {
        font-size: 18px;
        font-weight: 700;
        line-height: 19.8px;
        text-transform: uppercase;
        color: #272B2C;
        text-align: center;
    }
}

.goods-slide {
    position: relative;
    flex: 1 0 auto;
    height: 100%;

    &:nth-child(n+9) {
        @include minitablet {
            display: none;
        }
    }

    &--open {
        display: block !important;
    }

    @include minitablet {
        height: auto;
        width: calc(50% - 5px);
    }

    &__hidden {
        position: absolute;
        left: 0;
        top: 0;
        z-index: 1;
        border-radius: 24px;
        width: calc(100% - 30px);
        height: calc(100% - 30px);
    }

    &__content {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 15px;
        border: 1px solid #D4D4D4;
        border-radius: 24px;
        height: 100%;
        box-shadow: 4px 4px 14px 0px #0000001F;

        @include minitablet {
            box-shadow: none;
        }

        @include mobile {
            padding: 0 0 20px 0;
            border-radius: 16px;
        }
    }

    &__top {
        margin-bottom: 10px;

        @include mobile {
            margin-bottom: 20px;
        }
    }

    &__bottom {
        display: flex;
        flex-direction: column;
        min-height: 265px;

        @include bigdesktop {
            min-height: 320px;
        }

        @include desktop {
            min-height: 300px;
        }

        @include minitablet {
            min-height: 290px;
        }

        @include mobile {
            padding: 0 5px;
        }
    }

    &__subtitle {
        margin-bottom: auto;
        font-size: 18px;
        font-weight: 700;
        line-height: 19.8px;
        color: #272B2C;
        text-transform: uppercase;

        @include desktop {
            font-size: 16px;
            line-height: 17px;
        }

        @include minitablet {
            font-size: 14px;
            line-height: 15px;
        }

        @include mobile {
            padding-left: 5px;
            font-size: 12px;
            line-height: 13.2px;
        }
    }

    &__bottom-content {
        display: flex;
        flex-direction: column;
        margin-top: 5px;
    }

    &__img {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid #EFEFEF;
        border-radius: 24px 8px 24px 8px;
        height: 246px;
        cursor: pointer;

        @include mobile {
            border-radius: 16px;
            height: 180px;
        }

        img {
            @include mobile {
                max-height: 163px;
            }
        }

        &:hover {
            .goods-hidden {
                opacity: 1;
                visibility: visible;
            }
        }
    }

    &__img-mobile {
        position: absolute;
        top: 10px;
        right: 8px;
        display: none;

        .goods-hidden__icon {
            &:not(:last-child) {
                margin-bottom: 15px;
            }
        }

        @include tablet {
            display: block;
        }
    }

    &__icon {
        svg {
            width: 22px;
            height: 20px;

            @include mobile {
                width: 18.18px;
                height: 20px;
            }
        }

        &:not(:last-child) {
            margin-bottom: 20px;

            @include mobile {
                margin-bottom: 15px;
            }
        }
    }

    &__rating {
        @include mobile {
            padding-left: 5px;
        }
    }

    &__captions {
        position: absolute;
        bottom: 0;
        left: 0;
        display: flex;
        gap: 5px;
    }

    &__caption {
        padding: 5px;
        border-radius: 8px;
        font-size: 12px;
        font-weight: 700;
        line-height: 12px;
        color: #FFFFFF;
        text-transform: uppercase;

        &:nth-child(3) {
            @include mobile {
                display: none;
            }
        }

        &--blue {
            background-color: #0081CC;
        }

        &--green {
            background-color: #A3CD39;
        }

        &--orange {
            background-color: #FF5A00;
        }
    }

    &__btn {
        align-self: stretch;
        padding: 18px;
        border-radius: 16px;
        font-size: 16px;
        font-weight: 600;
        line-height: 100%;
        color: #FFFFFF;
        background-color: #DD3824;
        transition: background 0.3s ease-in-out;

        @include tablet {
            font-size: 14px;
        }

        @include minitablet {
            font-size: 12px;
        }

        @include mobile {
            padding: 12px 4px;
            border-radius: 8px;
            font-size: 10px;
        }

        &:focus {
            background-color: #C92B17;
        }

        &:hover {
            background-color: #C92B17;
        }
    }
}

.goods-hidden {
    padding: 15px;
    background-color: #F6F6F6F2;
    border-radius: 24px 8px 24px 8px;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.5s ease-in-out, visibility 0.5s ease-in-out;

    @include tablet {
        display: none;
    }

    &__block {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
    }

    &__item {
        &:not(:last-child) {
            margin-bottom: 15px;
        }
    }

    &__descr {
        margin-bottom: 5px;
        font-size: 14px;
        font-weight: 400;
        line-height: 14px;
        color: #272B2C;
    }

    &__text {
        font-size: 14px;
        font-weight: 700;
        line-height: 14px;
        color: #272B2C;
    }

    &__icon {
        svg {
            width: 22px;
            height: 20px;

            @include mobile {
                width: 18.18px;
                height: 20px;
            }
        }

        &:first-child {
            svg {
                fill: #DD3824;
            }
        }

        &:not(:last-child) {
            margin-bottom: 20px;
        }
    }
}

.price-block {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 20px;

    @include mobile {
        margin-bottom: 5px;
        flex-direction: column;
        align-items: flex-start;
        gap: 15px;
        padding-left: 5px;
    }

    &__right {
        display: flex;
        gap: 5px;
        align-items: center;
    }

    &__price {
        margin-bottom: 3px;
        font-size: 13px;
        font-weight: 400;
        line-height: 13px;
        color: #253242;
        text-decoration: line-through;

        @include mobile {
            font-size: 12px;
        }
    }

    &__main-price {
        font-size: 24px;
        font-weight: 700;
        line-height: 100%;
        color: #272B2C;

        @include mobile {
            font-size: 18px;
        }
    }

    &__svg {
        width: 18px;
        height: 18px;
    }

    &__text {
        font-size: 14px;
        font-weight: 700;
        line-height: 100%;
        color: #272B2C;

        @include mobile {
            font-size: 12px;
        }
    }
}

.basket {
    align-items: flex-start;
    display: flex;
    align-items: center;
    gap: 30px;
    margin-bottom: 20px;

    @include bigdesktop {
        flex-direction: column;
        gap: 15px;
        align-items: flex-start;
    }

    @include mobile {
        margin-bottom: 15px;
        padding-left: 5px;
    }

    &__left {
        display: flex;
        align-items: center;
        gap: 14px;

        @include mobile {
            gap: 11px;
        }
    }

    &__minus, &__plus {
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid #D4D4D4;
        border-radius: 8px;
        width: 36px;
        height: 36px;
        background-color: #F5F5F5;

        @include mobile {
            width: 30px;
            height: 30px;
        }
    }

    &__svg {
        width: 12px;
        height: 12px;
    }

    &__link {
        font-size: 16px;
        font-weight: 600;
        line-height: 100%;
        color: #DD3824;
        text-decoration: underline;

        @include tablet {
            font-size: 14px;
        }

        @include mobile {
            font-size: 12px;
        }
    }
}