.categories {
    padding: 130px 0;

    @include minitablet {
        padding: 70px 0;
    }

    &__title {
        color: #272B2C;
        max-width: 445px;
    }

    &__block {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: repeat(3, 1fr);
        gap: 18px;

        @include desktop {
            grid-template-columns: repeat(3, 1fr);
            grid-template-rows: repeat(4, 1fr);
        }

        @include minitablet {
            grid-template-columns: repeat(2, 1fr);
            //grid-template-rows: repeat(6, 1fr);
            grid-template-rows: auto;
        }
    }

    &__item {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
        padding: 20px;
        border: 1px solid #D4D4D4;
        border-radius: 24px;
        background-color: #F6F6F6;

        @include tablet {
            align-items: center;
        }

        @include mobile {
            padding: 20px 10px;
        }

        &:not(:last-child) {
            &::before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                z-index: 10;
                border-radius: 24px;
                width: 100%;
                height: 100%;
                background-color: transparent;
                transition: background 0.3s ease-in-out;
            }
            
            &:hover {
                &::before {
                    background-color: #DD3824;
                }

                .categories__descr {
                    color: #fff;
            
                    span {
                        color: #fff;
                    }
                }

                .categories__link {
                    color: #fff;
                }
    
                .categories__img {
                    border-radius: 100px;

                    img {
                        border-radius: 100px;
                    }
                }
            }
        }

        &:last-child {
            justify-content: center;
            align-items: center;
            border: none;
            background-color: transparent;
        }
    }

    &__main-link {
        position: absolute;
        left: 0;
        top: 0;
        z-index: 50;
        width: 100%;
        height: 100%;
    }

    &__top {
        position: relative;
        z-index: 10;
        margin-bottom: 25px;
        display: flex;
        align-items: center;
        gap: 20px;

        @include tablet {
            margin-bottom: 15px;
            flex-direction: column;
            gap: 15px;
        }
    }

    &__img {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 150px;
        height: 150px;
        min-width: 150px;
        background-color: #FFFFFF;
        border-radius: 24px;
        transition: border-radius 0.3s ease-in-out;

        @include bigdesktop {
            width: auto;
            height: auto;
            min-width: 100px;
        }

        @include mobile {
            width: 100px;
            height: 100px;
        }

        img {
            object-fit: cover;
            border-radius: 24px;
            transition: border-radius 0.3s ease-in-out;
        }
    }

    &__descr {
        font-size: 18px;
        font-weight: 400;
        line-height: 100%;
        color: #272B2C;
        transition: color 0.3s ease-in-out;

        @include tablet {
            font-size: 16px;
        }

        @include minitablet {
            font-size: 14px;
            text-align: center;
        }

        @include mobile {
            font-size: 12px;
        }

        span {
            color: #DD3824;
            transition: color 0.3s ease-in-out;
        }
    }

    &__link {
        position: relative;
        z-index: 100;
        margin-top: auto;
        font-size: 18px;
        font-weight: 700;
        line-height: 19.8px;
        color: #272B2C;
        text-transform: uppercase;
        //max-width: 200px;
        transition: color 0.3s ease-in-out;

        @include desktop {
            font-size: 16px;
            line-height: 17px;
        }

        @include tablet {
            font-size: 14px;
            line-height: 15px;
        }

        @include minitablet {
            font-size: 12px;
            line-height: 13.2px;
            text-align: center;
        }

        @include mobile {
            font-size: 10px;
            line-height: 11px;
        }
    }

    &__svg {
        margin-bottom: 24px;
        padding: 69px 60px 69px 70px;
        border-radius: 100px;
        border: 1px solid #0C5E84;
        width: 150px;
        height: 150px;

        @include tablet {
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 0;
        }

        @include mobile {
            width: 100px;
            height: 100px;
        }

        svg {
            height: 10.91px;
            width: 35.4px;

            @include tablet {
                width: 20px;
            }
        }
    }

    &__big-link {
        display: flex;
        flex-direction: column;
        align-items: center;

        &:hover .categories__svg-wrap {
            margin-left: -15px;
            padding-left: 15px;

            @include tablet {
                margin-left: 0;
                padding-left: 0;
            }
        }
    }

    &__link-descr {
        font-size: 18px;
        font-weight: 700;
        line-height: 19.8px;
        color: #272B2C;  
        text-transform: uppercase;

        @include tablet {
            font-size: 16px;
            line-height: 100%;
        }

        @include minitablet {
            font-size: 14px;
        }

        @include mobile {
            font-size: 12px;
        }
    }

    &__svg-wrap {
        display: inline-block;
        overflow: hidden;
        vertical-align: middle;
        transition: 0.3s margin-left linear, 0.3s padding-left linear;  

        svg {
            margin-right: 30px;
            margin-left: -15px;

            @include tablet {
                margin-right: 0;
                margin-left: 0;
            }
        }
    }
}