html {
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

body {
    &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        z-index: -1;
        width: 100%;
        height: 100%;
        background-color: transparent;
        transition: background 0.4s ease-in-out;
    }

    &.stop-scroll {
        position: relative;
		overflow: hidden;

        &--modal {
            &::before {
                z-index: 1000;
                background-color: #133C50F2;
                transition: background 0.4s ease-in-out;
            }
        }
	}
}

.page {
    height: 100%;
    font-family: var(--font-family, sans-serif);
    -webkit-text-size-adjust: 100%;
}

.page__body {
    margin: 0;
    min-width: 320px;
    min-height: 100%;
    font-size: 16px;
}

img {
    height: auto;
    max-width: 100%;
    object-fit: cover;
}

a {
    text-decoration: none;
}

.site-container {
    position: relative;
    overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
    display: none !important;
    /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
    border: none;
    padding: 0;
    background-color: transparent;
    cursor: pointer;
}

.list-reset {
    list-style: none;
    margin: 0;
    padding: 0;
}

.input-reset {
    -webkit-appearance: none;
    appearance: none;
    border: none;
    border-radius: 0;
    background-color: #fff;

    &::-webkit-search-decoration,
    &::-webkit-search-cancel-button,
    &::-webkit-search-results-button,
    &::-webkit-search-results-decoration {
        display: none;
    }
}

.visually-hidden {
    position: absolute;
    overflow: hidden;
    margin: -1px;
    border: 0;
    padding: 0;
    width: 1px;
    height: 1px;
    clip: rect(0 0 0 0);
}

.container {
    margin: 0 auto;
    padding: 0 var(--container-offset);
    max-width: var(--container-width);

    @include tablet {
        --container-offset: 30px; 
    } 

    @include minitablet {
        --container-offset: 15px; 
    } 
}

.js-focus-visible :focus:not(.focus-visible) {
    outline: none;
}

.centered {
    text-align: center;
}

.dis-scroll {
    position: fixed;
    left: 0;
    top: 0;
    overflow: hidden;
    width: 100%;
    height: 100vh;
    overscroll-behavior: none;
}

.page--ios .dis-scroll {
    position: relative;
}

.title {
    margin: 0;
    margin-bottom: 40px;
    font-size: 56px;
    font-weight: 600;
    line-height: 100%;
    color: #FFFFFF;
    text-transform: uppercase;

    @include desktop {
        font-size: 48px;
    }

    @include tablet {
        font-size: 42px;
    }

    @include minitablet {
        font-size: 36px;
    }

    @include mobile {
        font-size: 28px;
    }
}