.slider-nav {
    &__btn {
        position: absolute;
        top: 50%;
        z-index: 10;
        transform: translateY(-50%);

        &:focus {
            svg {
                outline: none;
            }
        }

        &:active {
            svg {
                outline: none;
            }
        }

        svg {
            width: 16px;
            height: 9px;
            fill: #fff;
            transition: fill 0.3s ease-in-out;
        }

        &::before {
            content: '';
            position: absolute;
            top: -20px;
            left: -20px;
            z-index: -1;
            width: 60px;
            height: 60px;
            border-radius: 50px;
            background-color: #DD3824;
        }
    }

    &__prev {
        left: 40px;
    }

    &__next {
        right: 50px;
    }
}

.swiper-button-disabled,
.swiper-button-disabled {
    cursor: auto;
    
    svg {
        fill: #272B2C;
    }

    &::before {
        background-color: #FFFFFF;
        box-shadow: 4px 4px 14px 0px #0000001F;
    }
}