.choose-city {
    width: 550px;

    @include mobile {
        width: calc(100% - 30px);
    }
    
    &__title {
        margin-bottom: 20px;
        font-size: 32px;
        font-weight: 600;
        line-height: 100%;
        text-align: center;
        color: #272B2C;
        text-transform: none;

        @include mobile {
            text-align: left;
        }
    }

    &__form {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 20px;
        padding: 13.5px 16px;
        border: 1px solid #D4D4D4;
        width: 100%;
    }

    &__label {
        width: 100%;
    }

    &__input {
        border: none;
        width: 100%;
        font-size: 16px;
        font-weight: 600;
        line-height: 100%;
        color: #272B2C;

        &:focus {
            outline: none;
        }

        &::placeholder {
            font-size: 16px;
            font-weight: 600;
            line-height: 100%;
            color: #D4D4D4;
        }
    }

    &__btn {
        width: 22px;
        height: 21px;
        background-image: url('../img/search.png');
        background-size: cover;
        background-position: right center;
        background-repeat: no-repeat;
    }

    &__block {
        padding-right: 8px;
        height: 252px;
    }

    &__links {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 0 10px;

        @include mobile {
            grid-template-columns: 1fr;
        }
    }

    &__link {
        padding: 10px 8px;
        border-radius: 8px;
        font-size: 16px;
        font-weight: 600;
        line-height: 100%;
        text-align: left;
        color: #272B2C;
        background-color: #fff;
        transform: color 0.3s ease-in-out, background 0.3s ease-in-out;

        &:hover {
            color: #0C5E84;
            background-color: #F6F6F6;
        }
    }
}

