.quick-view {
    padding: 56px 30px 30px 30px;
    width: 986px;

    @include mobile {
        padding: 46px 15px 40px 15px;
    }

    @include tablet {
        width: 700px;
    }

    @include minitablet {
        width: calc(100% - 30px);
    }

    &__block {
        display: flex;
        align-items: flex-start;
        gap: 30px;

        @include minitablet {
            flex-direction: column;
            align-items: stretch;
            gap: 20px;
        }
    }

    &__left {
        width: 442px;

        @include tablet {
            width: 50%;
        }

        @include minitablet {
            width: 100%;
        }
    }

    &__swiper {
        position: relative;
    }

    .swiper-pagination {
        bottom: 16px;

        @include mobile {
            bottom: 6px;
        }
    }

    .slider-nav {
        @include minitablet {
            display: none;
        }
    }

    &__prev {
        left: 15px;
        height: 6.55px;
        width: 12px;

        &::before {
            top: -6px;
            left: -7px;
            right: auto;
            bottom: auto;
            width: 30px;
            height: 30px;
        }
    }

    &__next {
        right: 25px;
        height: 6.55px;
        width: 12px;

        &::before {
            top: -6px;
            left: -7px;
            right: auto;
            bottom: auto;
            width: 30px;
            height: 30px;
        }
    }

    .goods-slide {
        &__img {
            display: flex;
            align-items: center;
            justify-content: center;
            border: none;
            border-radius: 24px;
            border: 1px solid #D4D4D4;
            width: 98%;
            height: 460px;

            @include mobile {
                height: 280px;
            }

            img {
                @include mobile {
                    //width: 60%;
                    width: 153px;
                    max-height: none !important;
                }
            }
        }

        &__top {
            @include mobile {
                margin-bottom: 0;
            }
        }

        &__captions {
            left: 20px;
            top: 20px;
            bottom: auto;

            @include mobile {
                left: 10px;
                top: 10px;
            }
        }

        &__img-mobile {
            top: 27px;
            right: 28px;
            display: block;

            @include mobile {
                top: 12px;
                right: 13px;
            }

            svg {
                fill: #272B2C;
            }
        }

        &__icon {
            &:not(:last-child) {
                @include mobile {
                    margin-bottom: 14px;
                }
            }
        }
    }

    &__title {
        margin-bottom: 20px;
        font-size: 36px;
        font-weight: 600;
        line-height: 100%;
        text-align: left;
        color: #272B2C;

        @include minitablet {
            font-size: 28px;
        }
    }

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 20px;

        @include tablet {
            flex-direction: column;
            align-items: flex-start;
        }
    }

    .rating {
        @include mobile {
            margin-bottom: 10px;
            flex-direction: row;
            align-items: center;
            gap: 6px;
        }
    }

    .price-block {
        width: 291px;

        &__price {
            text-align: left;
        }

        @include mobile {
            margin-bottom: 20px;
            flex-direction: row;
            width: 260px;
        }
    }

    .basket {
        margin-bottom: 0;
    }

    &__btn-wrap {
        display: flex;
        align-items: center;
        gap: 30px;
        margin-bottom: 30px;
        text-align: left;

        @include tablet {
            flex-direction: column;
            align-items: flex-start;
        }

        @include mobile {
            margin-bottom: 20px;
            align-items: center;
            gap: 20px;
        }
    }

    &__btn {
        padding: 18px 36px;
        border-radius: 16px;
        font-size: 16px;
        font-weight: 600;
        line-height: 100%;
        color: #fff;
        background-color: #DD3824;

        @include mobile {
            width: 100%;
        }
    }

    &__link {
        font-size: 16px;
        font-weight: 600;
        line-height: 100%;
        color: #DD3824;
        text-decoration: underline;
    }

    &__subtitle, &__feature-subtitle {
        margin-bottom: 10px;
        font-size: 18px;
        font-weight: 700;
        line-height: 100%;
        color: #272B2C;
        text-align: left;
    }

    &__descr {
        margin: 0;
        margin-bottom: 20px;
        font-size: 16px;
        font-weight: 400;
        line-height: 24px;
        text-align: left;
        color: #272B2C;

        @include tablet {
            font-size: 14px;
            line-height: 21px;
        }
    }

    &__table {
        margin-bottom: 20px;
    }

    &__table-item {
        display: grid;
        grid-template-columns: 3fr 1fr;
        padding: 10px 11px;
        border-radius: 8px;
        background-color: #F6F6F6;

        @include tablet {
            grid-template-columns: 2fr 1fr;
        }

        @include minitablet {
            grid-template-columns: 3fr 1fr;
        }

        &:nth-child(2n) {
            background-color: #FFF;
        }
    }

    &__feature-link {
        display: flex;
        align-items: center;
        gap: 10px;
        text-align: left;
    }

    &__link-text {
        position: relative;
        display: inline;
        font-size: 16px;
        font-weight: 600;
        line-height: 100%;
        text-align: left;
        text-transform: uppercase;
        color: #0C5E84;

        @include tablet {
            width: 220px;
        }

        @include minitablet {
            width: auto;
        }

        @include mobile {
            width: 220px;
        }
    }
}

.table-item {
    &__key {
        font-size: 14px;
        font-weight: 400;
        line-height: 100%;
        text-align: left;
        color: #272B2C;
    }

    &__value {
        font-size: 14px;
        font-weight: 700;
        line-height: 100%;
        text-align: left;
        color: #272B2C;
    }
}

.in-stock {
    display: flex;
    align-items: center;
    gap: 5px;

    &__svg {
        width: 18px;
        height: 18px;
    }

    &__text {
        font-size: 14px;
        font-weight: 700;
        line-height: 100%;
        color: #272B2C;
    }
}