.pumps {
    padding-bottom: 128px;

    &__container {
        position: relative;
        min-width: 0;

        @include mobile {
            padding: 0;
            margin: 0;
            width: 100%;
        }
    }

    &__swiper {
        border-radius: 24px;
    }

    .swiper-wrapper {
        align-items: stretch;
    }

    &__slide {
        flex: 1 0 auto;
        height: auto;
    }
    
    &__content {
        position: relative;
        z-index: 10;
        padding: 110px 40px 40px 40px;
        border-radius: 24px;
        background-color: #0C5E84;
        height: 100%;

        @include tablet {
            padding: 80px 30px 40px 30px;
        }

        @include mobile {
            padding: 60px 15px 30px 15px;
        }

        &::before {
            content: '';
            position: absolute;
            left: 23%;
            top: -40%;
            z-index: -1;
            width: 100%;
            height: 1398px;
            mix-blend-mode: soft-light;
            background: radial-gradient(50% 50% at 50% 50%, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%);

            @include mobile {
                top: 35px;
                left: -20%;
                right: -20%;
                width: 700px;
                height: 700px;
            }
        }
    }

    &__img {
        position: absolute;
        right: 40px;
        top: 58px;
        bottom: 40px;
        z-index: -1;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 532px;
        height: 702px;

        @include desktop {
            top: 0;
            height: 80%;
            width: 50%;
        }

        @include tablet {
            top: 0;
            right: 20px;
            width: 45%;
        }

        @include minitablet {
            top: auto;
            right: 40px;
            bottom: 100px;
            width: 70%;
        }

        @include mobile {
            right: auto;
            left: 50%;
            transform: translateX(-50%);
            width: 280px;
        }

        img {
            object-fit: contain;
        }

        &::after {
            content: '';
            position: absolute;
            right: 40px;
            top: 0;
            z-index: -2;
            width: 85%;
            height: 63.7%;
            background-image: url('../img/pumps-bg-1.png');
            background-position: right center;
            background-size: contain;
            background-repeat: no-repeat;
        }

        &--1 {
            &::after {
                @include tablet {
                    top: 50px;
                }

                @include mobile {
                    top: 200px;
                    right: auto;
                    left: 50%;
                    transform: translateX(-50%);
                    width: 290px;
                    height: 290px;
                }
            }

            @include tablet {
                bottom: 135px;
            }

            @include minitablet {
                bottom: 80px;
                width: 60%;
            }

            @include mobile {
                width: 150px;
                right: auto;
                left: 50%;
                bottom: 2%;
                transform: translateX(-50%);
            }
        }

        &--2 {
            &::after {
                right: 20px;
                width: 94%;
                height: 71.2%;
                background-image: url('../img/pumps-bg-2.png');

                @include desktop {
                    top: 60px;
                }
            }

            @include mobile {
                width: 270px;
                bottom: 90px;
            }
        }

        &--3 {
            &::after {
                top: 180px;
                right: 0;
                width: 95.2%;
                height: 29.7%;
                background-image: url('../img/pumps-bg-3.png');

                @include desktop {
                    top: 240px;
                }

                @include mobile {
                    right: 20px;
                }
            }
        }
    }

    &__title {
        margin-bottom: 10px;
        max-width: 62%;

        @include tablet {
            max-width: 70%;
        }

        @include minitablet {
            max-width: 100%;
        }

        @include mobile {
            font-size: 28px;
        }
    }

    &__descr {
        margin: 0;
        margin-bottom: 40px;
        font-size: 18px;
        font-weight: 400;
        line-height: 23.4px;
        color: #FFFFFF;
        max-width: 780px;

        @include bigdesktop {
            max-width: 50%;
        }

        @include tablet {
            max-width: 55%;
        }

        @include minitablet {
            max-width: 100%;
            font-size: 16px;
            line-height: 21px;
        }

        @include mobile {
            margin-bottom: 30px;
            font-size: 14px;
            line-height: 18.2px;
        }
    }

    &__link {
        display: inline-flex;
        align-items: center;
        gap: 10px;
        margin-bottom: 175px;
        padding: 18px 36px;
        border-radius: 16px;
        background-color: #DD3824;
        transition: background 0.3s ease-in-out;

        @include desktop {
            margin-bottom: 140px;
        }

        @include tablet {
            margin-bottom: 150px;
        }

        @include minitablet {
            margin-bottom: 400px;
        }

        @include mobile {
            display: flex;
            align-items: center;
            justify-content: center;
            width: calc(100% - 70px);
            margin-bottom: 320px;
            font-size: 16px;
        }

        @media (max-width: 400px) {
            margin-bottom: 282px;
        }

        &:focus {
            background-color: #C92B17;
        }

        &:hover {
            background-color: #C92B17;
        }

        &-text {
            font-size: 16px;
            font-weight: 600;
            line-height: 100%;
            color: #FFFFFF;
        }

        &-svg {
            width: 10.91px;
            height: 20px;
            fill: #FFFFFF;
        }
    }

    &__list {
        position: relative;
        z-index: 100;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 17px;

        &::-webkit-scrollbar { 
            @include tablet {
                width: 0;
            }
        }

        @include tablet {
            width: auto;
            overflow-x: scroll;
        }

        @include mobile {
            gap: 10px;
        }
    }

    &__item {
        display: flex;
        justify-content: space-between;
        gap: 16px;
        padding: 20px;
        border-radius: 24px;
        background-color: #F6F6F6;

        @include bigdesktop {
            flex-direction: column;
        }

        @include mobile {
            padding: 15px 10px;
            gap: 10px;
        }
    }

    .slider-nav {
        @include tablet {
            display: none;
        }
    }
}

.pumps-block {
    position: relative;
    z-index: 10;
    min-height: 170px;

    @include bigdesktop {
        height: 262px;
    }

    @include mobile {
        width: 150px;
    }

    &__main-link {
        position: absolute;
        z-index: 50;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
    }

    [mytitle] {
        &::after {
            content: '';
            display: block; 
            opacity: 0;
            visibility: hidden;
            transition: visibility 0.3s ease-in-out, opacity 0.3s ease-in-out;
        }

        &:hover::after {
            content: attr(mytitle);
            position: absolute;
            z-index: 1000;
            bottom: -200px;
            left: 0;
            opacity: 1;
            visibility: visible;
            padding: 16px;
            border-radius: 8px;
            width: 302px;
            font-size: 14px;
            font-weight: 400;
            line-height: 18.2px;
            color: #272B2C;
            background-color: #fff;
            text-transform: none;
            box-shadow: 4px 4px 14px 0px #0000001F;

            @include bigdesktop {
                bottom: -300px;
                width: 240px;
            }
        }
    }

    &__img {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 1.5px 2px;
        width: auto;
        height: auto;
        width: 100%;
        min-width: 100px;
        border-radius: 24px;
        background-color: #FFFFFF;

        @include bigdesktop {
            margin-top: auto;
        }

        @include tablet {
            padding: 0;
            width: 150px;
            height: 150px;
        }

        img {
            border-radius: 24px;
            width: auto;
            height: auto;
            min-width: 44px;
            max-height: 167px !important;
            object-fit: cover;
        }
    }

    &__content {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-between;
    }

    &__link {
        position: relative;
        z-index: 1000;
        font-size: 20px;
        font-weight: 700;
        line-height: 26px;
        color: #272B2C;
        text-transform: uppercase;

        @include bigdesktop {
            margin-bottom: 0;
        }

        @include desktop {
            font-size: 16px;
            line-height: 20px;
        }

        @include mobile {
            width: 100%;
            font-size: 14px;
            line-height: 18.2px;
        }

        &--low {
            span {
                text-transform: capitalize;
            }
        }

        @include bigdesktop {
            font-size: 18px;
            line-height: 24px;
        }

        @include tablet {
            font-size: 16px;
            line-height: 21px;
        }

        @include mobile {
            font-size: 14px;
            line-height: 18.2px;
        }
    }

    &__bottom {
        display: flex;
        align-items: center;
        gap: 8px;
        min-width: 202px;

        @include bigdesktop {
            width: 120px;
            min-width: 0px;
        }

        @include bigdesktop {
            display: none;
        }

        img {
            height: 10.91px;
            width: 20px;
        }

        &--hidden {
            display: none;

            @include bigdesktop {
                display: flex;
                width: auto;
                min-width: 202px;
            }

            @include mobile {
                min-width: 0;
                width: 110px;
            }
        }
    }

    &__text {
        font-size: 15px;
        font-weight: 600;
        line-height: 100%;
        color: #272B2C;
        text-transform: uppercase;

        @include desktop {
            font-size: 13px;
        }
    }
}

.swiper-horizontal>.swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal {
    bottom: -35px;
}

.swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
    margin: 0 var(--swiper-pagination-bullet-horizontal-gap, 5px);
}

.swiper-pagination-bullet {
    width: var(--swiper-pagination-bullet-width,var(--swiper-pagination-bullet-size,12px)) !important;
    height: var(--swiper-pagination-bullet-height,var(--swiper-pagination-bullet-size,12px)) !important;
    background-color: #EFEFEF;
    opacity: 1;
}

.swiper-pagination-bullet-active {
    --swiper-pagination-color: #DD3824 !important;
    --swiper-theme-color: #DD3824 !important;
    background: var(--swiper-pagination-color, var(--swiper-theme-color));
}