.about {
    padding: 130px 0;

    @include minitablet {
        padding: 70px 0;
    }

    &__container {
        @include mobile {
            padding: 0;
            margin: 0;
            width: 100%;
        }
    } 
    
    &__block {
        display: flex;
        gap: 100px;
        padding: 40px;
        border-radius: 24px;
        background-color: #F6F6F6;

        @include bigdesktop {
            gap: 40px;
        }

        @include tablet {
            display: grid;
            grid-template-columns: 4fr 3fr;
        }

        @include minitablet {
            grid-template-columns: 1fr;
            padding: 30px;
        }

        @include mobile {
            padding: 30px 15px;
        }
    }

    &__left {
        max-width: 570px;

        @include tablet {
            max-width: none;
        }
    }

    &__title {
        margin-bottom: 10px;
        color: #272B2C;
    }

    &__text {
        margin-bottom: 30px;
        font-size: 32px;
        font-weight: 600;
        line-height: 100%;
        color: #0C5E84;
        text-transform: uppercase;

        @include tablet {
            font-size: 28px;
        }

        @include minitablet {
            font-size: 24px;
        }

        @include mobile {
            font-size: 20px;
        }
    }

    &__descr {
        margin: 0;
        font-size: 18px;
        font-weight: 400;
        line-height: 23.4px;
        color: #272B2C;

        @include tablet {
            font-size: 16px;
            line-height: 21px;
        }

        @include mobile {
            font-size: 14px;
            line-height: 18.2px;
        }

        &:not(:last-child) {
            margin-bottom: 10px;
        }
    }

    &__img {
        position: relative;
        width: 100%;

        @include minitablet {
            max-width: 450px;
        }

        img {
            width: 100%;

            @include minitablet {
                max-width: 450px;
            }
        }

        &::before {
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            height: 9.5%;
            width: 32.5%;
            background-image: url('../img/about-logo.png');
            background-size: cover;
            background-position: left top;
            background-repeat: no-repeat;

            @include desktop {
                width: 73%;
            }
        }
    }
}