.order-call {
    width: 410px;

    @include mobile {
        width: calc(100% - 30px);
    }

    &__title {
        margin-bottom: 20px;
        font-size: 32px;
        font-weight: 600;
        line-height: 100%;
        text-align: center;
        color: #272B2C;
        text-transform: none;
    }

    &__form {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
    }

    &__input {
        margin-bottom: 16px;
        padding: 16px;
        border: 1px solid #D4D4D4;
        border-radius: 8px;
        outline: none;
        font-size: 16px;
        font-weight: 600;
        line-height: 100%;
        text-align: left;
        color: #272B2C;
        width: 100%;

        @include mobile {
            margin-bottom: 10px;
        }

        &:first-child {
            margin-bottom: 20px;
        }

        &::placeholder {
            color: #D4D4D4; 
        }
    }

    &__btn {
        padding: 18px 36px;
        border-radius: 16px;
        width: 100%;
        font-size: 16px;
        font-weight: 600;
        line-height: 100%;
        color: #FFFFFF;
        background-color: #DD3824;
    }

    &__checkbox-label {
        margin-bottom: 24px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 10px;
        width: 100%;

        @include mobile {
            margin-bottom: 16.5px;
        }
    }

    &__checkbox-descr {
        position: relative;
        font-size: 13px;
        font-weight: 400;
        line-height: 100%;
        color: #272B2C;
        text-align: left;

        a {
            color: #272B2C;
            text-decoration: underline;
        }
    }

    &__checkbox-label {
        position: relative;
        padding-left: 34px;
        cursor: pointer;
    }
    
    &__checkbox-check + .order-call__checkbox-span {
        position: absolute;
        left: 0;
        top: -2px;
        border-radius: 8px;
        border: 2px solid #D01818;
        width: 24px;
        height: 24px;
    }
    
    &__checkbox-check + .order-call__checkbox-span::before {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background-image: url(../img/tick.png);
        background-position: center;
        background-repeat: no-repeat;
        opacity: 0;
        transition: opacity 0.3s ease-in-out;
    }
    
    &__checkbox-check:checked + .order-call__checkbox-span::before {
        opacity: 1;
    }
}

